import { PrismaClient } from "@prisma/client";

const createMockPrismaClient = () => {
  return {
    account: {
      findMany: async () => {
        return [
          { id: "1", name: "Checking Account", balance: 2500.0, type: "checking" },
          { id: "2", name: "Savings Account", balance: 10000.0, type: "savings" },
          { id: "3", name: "Credit Card", balance: -450.0, type: "credit" },
        ];
      },
    },
    category: {
      findMany: async () => {
        return [
          { id: "1", name: "Groceries", type: "expense" },
          { id: "2", name: "Utilities", type: "expense" },
          { id: "3", name: "Dining", type: "expense" },
          { id: "4", name: "Salary", type: "income" },
        ];
      },
    },
    expense: {
      findMany: async () => {
        return [
          { 
            id: "1", 
            amount: 45.99, 
            description: "Grocery shopping", 
            date: new Date("2023-04-15"), 
            category: { id: "1", name: "Groceries", type: "expense" },
            account: { id: "1", name: "Checking Account", type: "checking" }
          },
          { 
            id: "2", 
            amount: 89.99, 
            description: "Phone bill", 
            date: new Date("2023-04-10"), 
            category: { id: "2", name: "Utilities", type: "expense" },
            account: { id: "1", name: "Checking Account", type: "checking" }
          },
          { 
            id: "3", 
            amount: 12.50, 
            description: "Coffee and snacks", 
            date: new Date("2023-04-05"), 
            category: { id: "3", name: "Dining", type: "expense" },
            account: { id: "3", name: "Credit Card", type: "credit" }
          },
        ];
      },
      create: async (data) => {
        console.log("Mock expense created:", data);
        return { id: "new-expense", ...data.data };
      },
    },
    income: {
      findMany: async () => {
        return [
          { 
            id: "1", 
            amount: 3500, 
            description: "Monthly Salary", 
            date: new Date("2023-04-01"), 
            category: { id: "4", name: "Salary", type: "income" },
            account: { id: "1", name: "Checking Account", type: "checking" }
          },
        ];
      },
      create: async (data) => {
        console.log("Mock income created:", data);
        return { id: "new-income", ...data.data };
      },
    },
    user: {
      findMany: async () => {
        return [
          { id: "1", name: "Demo User", email: "user@example.com" }
        ];
      },
    },
    $connect: async () => {},
    $disconnect: async () => {},
  };
};

const globalForPrisma = globalThis as unknown as {
  prisma: PrismaClient | ReturnType<typeof createMockPrismaClient> | undefined;
};

// Use a mock database in development when real database isn't available
let db: PrismaClient | ReturnType<typeof createMockPrismaClient>;

try {
  if (process.env.NODE_ENV === "production") {
    db = new PrismaClient();
  } else {
    if (!globalForPrisma.prisma) {
      try {
        globalForPrisma.prisma = new PrismaClient({
          log: ["query", "error", "warn"],
        });
      } catch (error) {
        console.warn("Failed to connect to database, using mock database:", error);
        globalForPrisma.prisma = createMockPrismaClient();
      }
    }
    db = globalForPrisma.prisma;
  }
} catch (error) {
  console.warn("Database connection error, using mock database:", error);
  db = createMockPrismaClient();
}

export { db };
