import { z } from "zod";

import { createTRPCRouter, publicProcedure } from "~/server/api/trpc";
import { Prisma } from "@prisma/client";

export const financeRouter = createTRPCRouter({
  // Get account summary
  getAccounts: publicProcedure.query(async ({ ctx }) => {
    try {
      const accounts = await ctx.db.account.findMany({
        include: {
          _count: {
            select: { expenses: true, incomes: true },
          },
        },
      });
      return accounts;
    } catch (error) {
      console.error("Error fetching accounts:", error);
      return [];
    }
  }),
  
  // Get categories
  getCategories: publicProcedure.query(async ({ ctx }) => {
    try {
      const categories = await ctx.db.category.findMany();
      return categories;
    } catch (error) {
      console.error("Error fetching categories:", error);
      return [];
    }
  }),
  
  // Get recent expenses
  getRecentExpenses: publicProcedure
    .input(z.object({ limit: z.number().default(10) }))
    .query(async ({ ctx, input }) => {
      try {
        const expenses = await ctx.db.expense.findMany({
          take: input.limit,
          orderBy: { date: "desc" },
          include: {
            category: true,
            account: true,
          },
        });
        return expenses;
      } catch (error) {
        console.error("Error fetching expenses:", error);
        return [];
      }
    }),
  
  // Get recent incomes
  getRecentIncomes: publicProcedure
    .input(z.object({ limit: z.number().default(10) }))
    .query(async ({ ctx, input }) => {
      try {
        const incomes = await ctx.db.income.findMany({
          take: input.limit,
          orderBy: { date: "desc" },
          include: {
            category: true,
            account: true,
          },
        });
        return incomes;
      } catch (error) {
        console.error("Error fetching incomes:", error);
        return [];
      }
    }),
  
  // Add new expense
  addExpense: publicProcedure
    .input(
      z.object({
        amount: z.number().positive(),
        description: z.string(),
        date: z.date(),
        userId: z.string(),
        accountId: z.string(),
        categoryId: z.string(),
      }),
    )
    .mutation(async ({ ctx, input }) => {
      try {
        const expense = await ctx.db.expense.create({
          data: input,
        });
        
        // Update account balance
        await ctx.db.account.update({
          where: { id: input.accountId },
          data: {
            balance: { decrement: input.amount },
          },
        });
        
        return expense;
      } catch (error) {
        console.error("Error adding expense:", error);
        throw error;
      }
    }),
  
  // Add new income
  addIncome: publicProcedure
    .input(
      z.object({
        amount: z.number().positive(),
        description: z.string(),
        date: z.date(),
        userId: z.string(),
        accountId: z.string(),
        categoryId: z.string(),
      }),
    )
    .mutation(async ({ ctx, input }) => {
      try {
        const income = await ctx.db.income.create({
          data: input,
        });
        
        // Update account balance
        await ctx.db.account.update({
          where: { id: input.accountId },
          data: {
            balance: { increment: input.amount },
          },
        });
        
        return income;
      } catch (error) {
        console.error("Error adding income:", error);
        throw error;
      }
    }),
}); 